import React, { Suspense } from 'react';
import { Routes, Route } from 'react-router-dom';

import './scss/app.scss';

import MainLayout from './layouts/MainLayout';
import { Home } from './pages/Home';

const Cart = React.lazy(() => import(/* webpackChunkName: "Cart" */ './pages/Cart'));
const FullPizza = React.lazy(() => import(/* webpackChunkName: "FullPizza" */ './pages/FullPizza'));
const NotFound = React.lazy(() => import(/* webpackChunkName: "NotFound" */ './pages/NotFound'));



function App() {

    React.useEffect(() => {
        window.alert('Это приложение построено с помощью React JS, в качестве state managere используется Redux Toolkit и Redux Thunk для асинхонных операций, также используется TypeScript для типизации.Для стилей использовался препроцессор SASS, также использовались такие распространенные библиотеки как:axios,lodash.debounce,react-content-loader,react-paginate,react-router-dom,styled-components')
    },[])
    return (
        <Routes>
            <Route
                path='/'
                element={<MainLayout />}
            >
                <Route
                    path=''
                    element={<Home />}
                />
                <Route
                    path='cart'
                    element={
                        <Suspense fallback={<div>Loading...</div>}>
                            <Cart />
                        </Suspense>
                    }
                />
                <Route
                    path='pizza/:id'
                    element={
                        <Suspense fallback={<div>Loading...</div>}>
                            <FullPizza />
                        </Suspense>
                    }
                />
                <Route
                    path='*'
                    element={
                        <Suspense fallback={<div>Loading...</div>}>
                            <NotFound />
                        </Suspense>
                    }
                />
            </Route>
        </Routes>
    );
}

export default App;
